<template>
  <div>
    <h3 align="center"><a-icon type="form" />航班管理</h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="flightFormRef" :rules="rules" :model="formdata" class="flightForm">
        <a-col :span="4">
          <a-form-model-item prop="flight_num">
            <a-input v-model="formdata.flight_num" placeholder="航班号"></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="3">
          <a-form-model-item prop="price">
            <a-input type="number" :min="0" v-model="formdata.price" placeholder="价格"></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="3">
          <a-form-model-item prop="num_seats">
            <a-input type="number" :min="0" v-model="formdata.num_seats" placeholder="座位数"></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item prop="from_city">
            <a-input v-model="formdata.from_city" placeholder="出发地"></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item prop="ariv_city">
            <a-input v-model="formdata.ariv_city" placeholder="目的地"></a-input>
          </a-form-model-item>
        </a-col>
        <a-col :span="4">
          <a-form-model-item class="flightBtn">
            <a-button type="primary" @click="addFlight">添加</a-button>
          </a-form-model-item>
        </a-col>
        </a-form-model>
      </a-row>

      <a-table :rowKey="flight_num" :columns="columns" :dataSource="flightList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delFlight(data.flight_num)">删除</a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '航班号',
    dataIndex: 'flight_num',
    width: '15%',
    align: 'center',
    key: 'flight_num'
  },
  {
    title: '价格',
    dataIndex: 'price',
    width: '12%',
    align: 'center',
    key: 'price'
  },
  {
    title: '座位数',
    dataIndex: 'num_seats',
    width: '12%',
    align: 'center',
    key: 'num_seats'
  },
  {
    title: '出发地',
    dataIndex: 'from_city',
    width: '20%',
    align: 'center',
    key: 'from_city'
  },
  {
    title: '目的地',
    dataIndex: 'ariv_city',
    width: '20%',
    align: 'center',
    key: 'ariv_city'
  },
  {
    title: '操作',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        flight_num: '',
        price: '',
        num_seats: '',
        from_city: '',
        ariv_city: ''
      },
      rules: {
        flight_num: [
          { required: true, message: '请输入航班号!', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '请输入价格!', trigger: 'blur' }
        ],
        num_seats: [
          { required: true, message: '请输入座位数!(正整数)', trigger: 'blur' }
        ],
        from_city: [
          { required: true, message: '请输入出发地!', trigger: 'blur' }
        ],
        ariv_city: [
          { required: true, message: '请输入目的地!', trigger: 'blur' }
        ]
      },
      flightList: [],
      columns
    }
  },
  created () {
    this.getFlightList()
  },
  methods: {
    async getFlightList () {
      const { data: res } = await this.$http.get('flights/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.flightList = res.data
    },
    async addFlight () {
      this.$refs.flightFormRef.validate(async valid => {
        this.$confirm({
          title: '添加操作',
          content: '确定要添加该航班吗?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('输入非法数据，请重新输入!')
            const { data: res } = await this.$http.post('flight/addFlight', {
              flight_num: this.formdata.flight_num,
              price: this.formdata.price - 0,
              num_seats: this.formdata.num_seats - 0,
              from_city: this.formdata.from_city,
              ariv_city: this.formdata.ariv_city
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('添加成功!')
            this.getFlightList()
          },
          onCancel: () => {
            this.$message.info('已取消添加!')
          }
        })
      })
    },
    async delFlight (num) {
      this.$confirm({
        title: '确定要删除该航班吗?(航班号:' + `${num}` + ')',
        content: '一旦删除，无法恢复',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`flight/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('删除成功!')
          this.getFlightList()
        },
        onCancel: () => {
          this.$message.info('已取消删除!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
