<template>
  <div>
    <h3 align="center"><a-icon type="form" />宾馆管理</h3>
    <a-card>
      <a-row gutter="30">
        <a-form-model ref="hotelFormRef" :rules="rules" :model="formdata" class="hotelForm">
          <a-col :span="4">
            <a-form-model-item prop="hotel_name">
              <a-input v-model="formdata.hotel_name" placeholder="宾馆名"></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="3">
            <a-form-model-item prop="price">
              <a-input type="number" :min="0" v-model="formdata.price" placeholder="价格"></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="3">
            <a-form-model-item prop="num_rooms">
              <a-input type="number" :min="0" v-model="formdata.num_rooms" placeholder="房间数"></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item prop="city_name">
              <a-input v-model="formdata.city_name" placeholder="城市"></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :span="4">
            <a-form-model-item class="hotelBtn">
              <a-button type="primary" @click="addHotel">添加</a-button>
            </a-form-model-item>
          </a-col>
        </a-form-model>
      </a-row>

      <a-table :rowKey="hotel_name" :columns="columns" :dataSource="hotelList" bordered>
        <template slot="action" slot-scope="data">
          <div class="actionSlot">
            <a-button type="danger" @click="delHotel(data.hotel_name)">删除</a-button>
          </div>
        </template>
      </a-table>
    </a-card>
  </div>
</template>

<script>
const columns = [
  {
    title: '宾馆名',
    dataIndex: 'hotel_name',
    width: '15%',
    align: 'center',
    key: 'hotel_name'
  },
  {
    title: '价格',
    dataIndex: 'price',
    width: '15%',
    align: 'center',
    key: 'price'
  },
  {
    title: '房间数',
    dataIndex: 'num_rooms',
    width: '15%',
    align: 'center',
    key: 'num_rooms'
  },
  {
    title: '城市',
    dataIndex: 'city_name',
    width: '20%',
    align: 'center',
    key: 'city_name'
  },
  {
    title: '操作',
    width: '20%',
    align: 'center',
    key: 'action',
    scopedSlots: { customRender: 'action' }
  }
]

export default {
  data () {
    return {
      formdata: {
        hotel_name: '',
        price: '',
        num_rooms: '',
        city_name: ''
      },
      rules: {
        hotel_name: [
          { required: true, message: '请输入宾馆名!', trigger: 'blur' }
        ],
        price: [
          { required: true, message: '请输入价格!', trigger: 'blur' }
        ],
        num_rooms: [
          { required: true, message: '请输入房间数!', trigger: 'blur' }
        ],
        city_name: [
          { required: true, message: '请输入城市!', trigger: 'blur' }
        ]
      },
      hotelList: [],
      columns
    }
  },
  created () {
    this.getHotelList()
  },
  methods: {
    async getHotelList () {
      const { data: res } = await this.$http.get('hotels/list')
      if (res.status !== 200) return this.$message.error(res.message)
      this.hotelList = res.data
    },
    async addHotel () {
      this.$refs.hotelFormRef.validate(async valid => {
        this.$confirm({
          title: '添加操作',
          content: '确定要添加该宾馆吗?',
          onOk: async () => {
            console.log(this.formdata)
            if (!valid) return this.$message.error('输入非法数据，请重新输入!')
            const { data: res } = await this.$http.post('hotel/addHotel', {
              hotel_name: this.formdata.hotel_name,
              price: this.formdata.price - 0,
              num_rooms: this.formdata.num_rooms - 0,
              city_name: this.formdata.city_name
            })
            if (res.status !== 200) return this.$message.error(res.message)
            this.$message.success('添加成功!')
            this.getHotelList()
          },
          onCancel: () => {
            this.$message.info('已取消添加!')
          }
        })
      })
    },
    async delHotel (num) {
      this.$confirm({
        title: '确定要删除该宾馆吗?(宾馆名:' + `${num}` + ')',
        content: '一旦删除，无法恢复',
        onOk: async () => {
          const { data: res } = await this.$http.delete(`hotel/${num}`)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('删除成功!')
          this.getHotelList()
        },
        onCancel: () => {
          this.$message.info('已取消删除!')
        }
      })
    }
  }
}
</script>

<style scoped>
.actionSlot {
  display: flex;
  justify-content: center;
}
</style>
