<template>
  <a-layout-sider>
    <div class="log">
      <h4><a-icon type="account-book" />旅游预订系统</h4>
    </div>
    <a-menu theme="dark" mode="inline" @click="goToPage">
      <a-menu-item key="index">
        <a-icon type="appstore"></a-icon>
        <span>菜单</span></a-menu-item>
      <a-sub-menu>
        <span slot="title"><a-icon type="user"/><span>个人中心</span></span>
        <a-menu-item key="myResv">
          <a-icon type="contacts"/>
          <span>我的预约</span></a-menu-item>
        <a-menu-item key="myRoute">
          <a-icon type="calendar"/>
          <span>我的行程</span></a-menu-item>
      </a-sub-menu>
      <a-menu-item key="flightList">
        <span><a-icon type="rocket"/>预订航班</span>
      </a-menu-item>
      <a-menu-item key="carList">
        <span><a-icon type="car"/>预订出租车</span>
      </a-menu-item>
      <a-menu-item key="hotelList">
        <span><a-icon type="home"/>预订宾馆</span>
      </a-menu-item>
      <a-menu-item id="flightManager" key="flightManager">
        <span><a-icon type="rocket"/>航班管理</span>
      </a-menu-item>
      <a-menu-item id="carManager" key="carManager">
        <span><a-icon type="car"/>出租车管理</span>
      </a-menu-item>
      <a-menu-item id="hotelManager" key="hotelManager">
        <span><a-icon type="home"/>宾馆管理</span>
      </a-menu-item>
    </a-menu>
  </a-layout-sider>
</template>

<script>
export default {
  methods: {
    goToPage (item) {
      var custName = window.sessionStorage.getItem('cust_name')
      if (custName !== 'admin' && (item.key === 'flightManager' || item.key === 'carManager' || item.key === 'hotelManager')) {
        return this.$message.error('没有管理员权限!')
      } else {
        this.$router.push('/' + item.key).catch((err) => err)
      }
    }
  }
}
</script>

<style scoped>
.log {
  height: 64px;
  width: 200px;
  background-color: #eeeeee;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
}
</style>
